import * as SMSApi from '@/api/SMS';
export default {
  props: {
    value: {
      type: String,
      default: undefined
    },
    phone: {
      type: String,
      default: undefined
    }
  },
  data: function data() {
    return {
      sendSMSText: '获取验证码',
      hasSendSMS: false
    };
  },
  methods: {
    handleSendSmsClick: function handleSendSmsClick() {
      var _this = this;

      // check phone
      if (this.phone === undefined) {
        this.$message.error('请输入正确的手机号后再获取验证码');
        return;
      }

      SMSApi.sendSMSCode(this.phone).then(function (res) {
        if (res.status) {
          _this.disableSendSmsBtn();
        }
      });
    },
    handleChange: function handleChange(value) {
      this.$emit('change', value);
    },
    disableSendSmsBtn: function disableSendSmsBtn() {
      var _this2 = this;

      var num = 60;
      this.sendSMSText = num + 's';
      var timer = setInterval(function () {
        num--;

        if (num <= 0) {
          clearInterval(timer);
          _this2.hasSendSMS = false;
          _this2.sendSMSText = '重新发送';
        } else {
          _this2.hasSendSMS = true;
          _this2.sendSMSText = num + 's';
        }
      }, 1000);
    }
  }
};