import AcademySelector from '@/components/Selector/Academy';
import ClassroomSelector from '@/components/Selector/Classroom';
export default {
  components: {
    AcademySelector: AcademySelector,
    ClassroomSelector: ClassroomSelector
  },
  data: function data() {
    return {
      form: this.$form.createForm(this, {
        name: 'basicInfoForm'
      }),
      submitLoading: false,
      labelCol: {
        lg: {
          span: 5
        },
        sm: {
          span: 5
        }
      },
      wrapperCol: {
        lg: {
          span: 19
        },
        sm: {
          span: 19
        }
      }
    };
  },
  methods: {
    nextStep: function nextStep() {
      var _this = this;

      var validateFields = this.form.validateFields; // 先校验，通过表单校验后，才进入下一步

      validateFields(function (err, values) {
        if (!err) {
          console.log(values);

          _this.$emit('form', values);

          _this.$emit('nextStep');
        }
      });
    }
  }
};