import { firstLoginUpdate } from '@/api/user';
import SendSMS from '@/components/PhoneSMS/SendSMS';
export default {
  components: {
    SendSMS: SendSMS
  },
  data: function data() {
    return {
      labelCol: {
        lg: {
          span: 5
        },
        sm: {
          span: 5
        }
      },
      wrapperCol: {
        lg: {
          span: 19
        },
        sm: {
          span: 19
        }
      },
      form: this.$form.createForm(this),
      loading: false,
      phoneNum: undefined
    };
  },
  methods: {
    phoneCheck: function phoneCheck(rule, value, callbackFn) {
      var reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;

      if (!reg.test(value)) {
        callbackFn('请输入正确的手机号码');
        return;
      }

      this.phoneNum = value;
      callbackFn();
    },
    nextStep: function nextStep() {
      var _this = this;

      var that = this;
      var validateFields = this.form.validateFields;
      that.loading = true;
      validateFields(function (err, values) {
        if (!err) {
          that.$emit('form', values);
          firstLoginUpdate(values).then(function (res) {
            var status = res.status;

            if (status) {
              that.$emit('nextStep');
            } else {
              that.$message.error(res.message);
            }

            _this.$emit('result', status);
          }).finally(function () {
            _this.loading = false;
          }); // 提交
        }
      });
    },
    prevStep: function prevStep() {
      this.$emit('prevStep');
    }
  }
};